import * as React from "react"
import { Link } from "gatsby"
import styled from 'styled-components'

const StyledWrapper = styled.div`
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  background: rgb(27, 38, 44);
  font-family: Poppins;
  font-weight: 700
`

const StyledWidget = styled(Link)`
  color: rgb(222, 222, 222);
  border: 3px solid rgb(232, 93, 117);
  border-radius: 5px;
  text-decoration: none;
  font-size: 1.2rem;
  padding: 20px 40px;
  margin: 50px;
  cursor: pointer;
  letter-spacing: 1px;
  &:hover {
    color: rgb(27,38,44);
    background: rgb(222, 222, 222);
    border: 3px solid rgb(222, 222, 222);
  }
`
const IndexPage = () => (
  <StyledWrapper>
    <StyledWidget to="/duplicator/">Duplikaty</StyledWidget>
    <StyledWidget to="/linker/">Linki</StyledWidget>
  </StyledWrapper>
)

export default IndexPage
